import { useState } from 'react'

export default function MenuItem({onClick, children, onMouseEnter, onMouseLeave}) {
    const [hover, setHover] = useState(false)

    const handleHover = () => {
        setHover(!hover)
    }


    return (
        <div>
            <div className="menu_item">
                <div
                    className="items"
                    onMouseEnter = { onMouseEnter }
                    onMouseLeave = { onMouseLeave }
                    onClick={onClick}
                >
                    {children}
                </div>
            </div>
            <style jsx>
            {`
                .menu_item {
                    animation: 1s appear forwards;
                }
                .items{
                    font-size: 25px;
                    padding: 1rem 0;
                    margin: 0 5%
                    cursor: pointer;
                    color: ${hover ? 'gray':'green'};
                    transition: color 0.2s ease-in-out;
                    animation: 0.5s slideIn forwards;
                }
                
                
                `}
            </style>
        </div>
    )
}
